import styled from 'styled-components';
import { Layout, Typography, Collapse as AntCollapse } from 'antd';

const { Footer: AntFooter } = Layout;
const { Title } = Typography;

const TwoColFlex = styled.div`
  display: grid;
  gap: 2rem;
  grid-template-columns: 1fr 1fr;

  @media (max-width: 692px) {
    gap: 1rem;
  }
`;

const ColumnContainer = styled.div`
  display: flex;
  flex-direction: column;
  gap: 0;
`;

const Grade = styled.h2`
  font-weight: bold;
  font-size: 3rem;
  margin-bottom: 0;
`;

const Footer = styled(AntFooter)`
  text-align: center;
  border-top: 1px solid rgb(216, 216, 216);
  background-color: white;
  width: 100%;
`;

const FrontBackGallery = styled.div`
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr 1fr;
`;
const SidesGallery = styled.div`
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr 1fr;
`;

const Collapse = styled(AntCollapse)`
  & .ant-collapse-header,
  & .ant-collapse-content-box {
    padding-left: 0 !important;
  }
`;

const CollapsibleHeader = styled(Title)`
  display: inline;
  margin-bottom: 0;
`;

const DropdownStyleWrapper = styled.div.attrs(() => ({
  className: 'ant-select ant-select-borderless ant-select-single ant-select-show-arrow',
}))`
  border-bottom: 1px solid rgb(216, 216, 216);
  padding-bottom: 0.5rem;
  margin-bottom: 0rem;

  cursor: unset;
`;

const DropdownStyleContainer = ({ children }) => (
  <DropdownStyleWrapper>
    <div className='ant-select-selector'>
      <span className='ant-select-selection-item'>{children}</span>
    </div>
  </DropdownStyleWrapper>
);

const CompareDetailsWrapper = styled.div`
  & .ant-select {
    font-size: 1rem;
    font-weight: bold;
    margin-bottom: 1rem !important;
  }

  @media (max-width: 692px) {
    & .ant-select {
      font-size: 1em;
    }
  }

  & .ant-select-selector {
    padding-left: 0 !important;
  }
`;

const CompareSelectOption = ({ test_id, start_time }) => (
  <div style={{ display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap', gap: '1rem' }}>
    <span>{test_id}</span>
    <span style={{ color: 'grey', fontWeight: 'normal' }}>{start_time}</span>
  </div>
);

export {
  Collapse,
  CollapsibleHeader,
  ColumnContainer,
  Footer,
  FrontBackGallery,
  Grade,
  SidesGallery,
  TwoColFlex,
  DropdownStyleContainer,
  CompareDetailsWrapper,
  CompareSelectOption,
};
