import { Col, Divider, Row, Select, Typography } from 'antd';
import { Fragment, useState } from 'react';
import { ImageGrade } from './ImageGrade';

import { ColumnContainer, CompareDetailsWrapper, CompareSelectOption, Grade } from './styled';

const { Title } = Typography;

export const XTRCompare = ({ searchResults }) => {
  // const [selected, setSelected] = useState(searchResults[0].test_id);

  // const currrent = searchResults.find(({ test_id }) => test_id === selected);

  // const options = searchResults.map((props) => ({
  //   label: <CompareSelectOption test_id={props.test_id} start_time={props.start_time} />,
  //   value: props.test_id,
  // }));

  const { before, after } = searchResults.reduce(
    (col, current) => {
      const item = (
        <CompareSelectOption test_id={current.test_id} start_time={current.start_time} />
      );

      if (current.flag === 'BEFORE') {
        col.before.push(current);
        return col;
      }

      col.after.push(current);
      return col;
    },
    { before: [], after: [] }
  );

  return (
    <Fragment>
      <Title level={3}>Details</Title>
      <Row gutter={16}>
        <Col span={12}>
          <DeviceComparison options={before} />
        </Col>
        <Col span={12}>
          <DeviceComparison options={after} title='After' />
        </Col>
      </Row>
    </Fragment>
  );
};

export const DeviceComparison = ({ options, title = 'Before' }) => {
  const [selected, setSelected] = useState(options[0]?.test_id);

  const device = options.find(({ test_id }) => test_id === selected);

  const selectOptions = options.map((item) => ({
    label: <CompareSelectOption test_id={item.test_id} start_time={item.start_time} />,
    value: item.test_id,
  }));

  return (
    <CompareDetailsWrapper>
      <ColumnContainer style={{ minHeight: '50vh', textAlign: 'left', gap: '2rem' }}>
        <Row>
          <Col span={6}>
            <Typography.Title level={3}>{title}</Typography.Title>
          </Col>
          <Col span={18}>
            <Select
              style={{
                borderBottom: '1px solid rgb(216, 216, 216)',
                paddingBottom: '0.5rem',
                marginBottom: '1rem',
                width: '100%',
              }}
              bordered={false}
              options={selectOptions}
              onSelect={setSelected}
              value={selected}
            />
          </Col>
        </Row>

        {device && <ImageCollection device={device} />}
      </ColumnContainer>
    </CompareDetailsWrapper>
  );
};

const ImageCollection = ({ device }) => {
  const sideKeysBefore = [
    { label: 'Top', old_image: 'old_image_top', image: 'image_top', grade: 'grade_top' },
    {
      label: 'Bottom',
      old_image: 'old_image_bottom',
      image: 'image_bottom',
      grade: 'grade_bottom',
    },
    { label: 'Left', old_image: 'old_image_left', image: 'image_left', grade: 'grade_left' },
    { label: 'Right', old_image: 'old_image_right', image: 'image_right', grade: 'grade_right' },
  ];

  const testDetails = [
    { key: 'test_id', label: 'Test ID' },
    { key: 'start_time', label: 'Start Time' },
    { key: 'end_time', label: 'End Time' },
    { key: 'elapsed_time', label: 'Elapsed Time' },
    { key: 'test_profile', label: 'Test Profile' },
  ];

  return (
    <>
      <div>
        {testDetails.map(({ label, key }) => (
          <h4 style={{ margin: '4px' }}>
            <b>{label}</b>
            <br />
            <span>{device[key] || '--'}</span>
          </h4>
        ))}
      </div>

      <div style={{ height: '20px' }}>
        <Typography.Title level={3} style={{ marginBottom: 0 }}>
          Overall Grade <span style={{ color: 'gray' }}>{device.grade_overall || '-'}</span>
        </Typography.Title>
      </div>

      <ImageGrade label={'Front'} grade={device.grade_front} image={device.image_front} />
      <ImageGrade label={'Back'} grade={device.grade_back} image={device.image_back} />

      <Divider style={{ marginTop: 0 }} />

      <div style={{ height: '20px' }}>
        <Typography.Title level={3} style={{ marginBottom: 0 }}>
          Sides Grade <span style={{ color: 'gray' }}>{device.grade_sides || '-'}</span>
        </Typography.Title>
      </div>

      {sideKeysBefore.map((item) => (
        <ImageGrade
          key={item.label}
          label={item.label}
          grade={device[item.grade]}
          image={device[item.image]}
        />
      ))}
    </>
  );
};
