import axiosClient from './axiosClient';

const intl = new Intl.DateTimeFormat('en-US', {
  year: 'numeric',
  month: 'numeric',
  day: 'numeric',
  hour: 'numeric',
  minute: 'numeric',
  second: 'numeric',
  hour12: false,
});

export async function findByImei(query) {
  const { data } = await axiosClient.get(`/${query}`);

  const arr = Array.isArray(data) ? data : [data];

  return arr.map((item) => {
    return {
      test_date: item.start_time && intl.format(new Date(item.start_time)),
      test_id: item.id,
      imei_sn: item.imei,
      product_name: item.sku,
      model: item.model,
      firmware: item.hwVer,
      software: item.swVer,
      grade_overall: item.gradeOverall,
      grade_front: item.gradeFront,
      grade_back: item.gradeBack,
      grade_top: item.gradeTop,
      grade_bottom: item.gradeBottom,
      grade_left: item.gradeLeft,
      grade_right: item.gradeRight,
      // current images
      image_front: item.picFront,
      image_back: item.picBack,
      image_top: item.picTop,
      image_bottom: item.picBottom,
      image_left: item.picLeft,
      image_right: item.picRight,
      flag: item.flag,
      elapsed_time: item.elapsedTime,
      start_time: item.etime && intl.format(new Date(item.stime)),
      end_time: item.stime && intl.format(new Date(item.etime)),
      test_profile: item.machineId,
    };
  });
}
