import { Divider, Select, Typography } from 'antd';
import { useState } from 'react';
import { formatImageGradeData } from '../services/imagesService';
import {
  ColumnContainer,
  CompareDetailsWrapper,
  CompareSelectOption,
  DropdownStyleContainer,
  Grade,
  TwoColFlex,
} from './styled';
import { ImageGrade } from './ImageGrade';

const { Title } = Typography;

export const TelusCompare = ({ searchResults }) => {
  const [latest, ...options] = searchResults;
  const [selected, setSelected] = useState(options[0].test_id);

  const currrent = options.find(({ test_id }) => test_id === selected);

  return (
    <TwoColFlex>
      <TelusCompareDetail current selected={latest.test_id} data={latest} options={[latest]} />
      <TelusCompareDetail
        selected={selected}
        setSelected={setSelected}
        data={currrent}
        options={options}
      />
    </TwoColFlex>
  );
};

export const TelusCompareDetail = (props) => {
  const { data = {}, selected, setSelected, options = [], current } = props;
  const { grade_overall, start_time, grade_sides } = data;

  const optionsMap = options.map((props) => ({
    label: <CompareSelectOption test_id={props.test_id} start_time={props.start_time} />,
    value: props.test_id,
  }));

  const frontBackKeys = [
    { label: 'Front', image: 'image_front', grade: 'grade_front' }, // Front
    { label: 'Back', image: 'image_back', grade: 'grade_back' }, // Back
  ];

  const sideKeys = [
    { label: 'Top', image: 'image_top', grade: 'grade_top' },
    { label: 'Bottom', image: 'image_bottom', grade: 'grade_bottom' },
    { label: 'Left', image: 'image_left', grade: 'grade_left' },
    { label: 'Right', image: 'image_right', grade: 'grade_right' },
  ];

  const testDetails = [
    { key: 'test_id', label: 'Test ID' },
    { key: 'start_time', label: 'Start Time' },
    { key: 'end_time', label: 'End Time' },
    { key: 'elapsed_time', label: 'Elapsed Time' },
    { key: 'test_profile', label: 'Test Profile' },
  ];

  const frontBack = frontBackKeys.map((side) => formatImageGradeData(data, side));
  const sides = sideKeys.map((side) => formatImageGradeData(data, side));

  return (
    <CompareDetailsWrapper>
      <ColumnContainer style={{ minHeight: '50vh', textAlign: 'left', gap: '2rem' }}>
        {current ? (
          <DropdownStyleContainer>
            <CompareSelectOption test_id={`Current`} start_time={start_time} />
          </DropdownStyleContainer>
        ) : (
          <Select
            style={{
              borderBottom: '1px solid rgb(216, 216, 216)',
              paddingBottom: '0.5rem',
              marginBottom: '1rem',
            }}
            disabled={current}
            bordered={false}
            options={optionsMap}
            onSelect={setSelected}
            value={selected}
          />
        )}

        <div>
          <Title level={3}>Details</Title>
          {testDetails.map(({ label, key }) => (
            <div key={key} style={{ marginBottom: '1rem' }}>
              <h4>
                <b>{label}</b>
                <br />
                <span>{data[key] || '-'}</span>
              </h4>
            </div>
          ))}
        </div>

        <div>
          <Title level={3} style={{ marginBottom: 0 }}>
            Overall Grade
          </Title>
          <Grade>{grade_overall || '-'}</Grade>
        </div>

        {frontBack.map((props) => (
          <ImageGrade key={props.label} {...props} />
        ))}
        <Divider style={{ marginTop: 0 }} />

        <div>
          <Title level={3} style={{ marginBottom: 0 }}>
            Sides Grade
          </Title>
          <Grade>{grade_sides || '-'}</Grade>
        </div>

        {sides.map((props) => (
          <ImageGrade key={props.label} {...props} />
        ))}
      </ColumnContainer>
    </CompareDetailsWrapper>
  );
};
