import { Image } from 'antd';

const originalHost =
  'http://a4bb161d7245b98798a3-aec0211d0803c5ff3e93d45eb366af57.r19.cf1.rackcdn.com';

// FIXME: Missing Images spacing on compare
export const ImageGrade = ({ label, grade = '', image: origImage }) => {
  const image = origImage?.replace(originalHost, '/images');

  return (
    <div>
      <h3 style={{ height: '25px' }}>
        <b>{label}</b> <span style={{ color: 'grey' }}>{grade || '-'}</span>
      </h3>

      <Image
        preview={!!image}
        style={{ width: '100%', borderRadius: '15px' }}
        src={image || ''}
        alt=''
      />
    </div>
  );
};
