import {
  Collapse as AntCollapse,
  Descriptions,
  Divider,
  Empty,
  Input,
  Layout,
  Result,
  Skeleton,
  Tabs,
  Row,
  Col,
  Typography,
} from 'antd';
import { useState } from 'react';
import {
  Collapse,
  CollapsibleHeader,
  ColumnContainer,
  Footer,
  FrontBackGallery,
  Grade,
  SidesGallery,
} from './components/styled';
import TelusLogo from './logo.svg';
// import CynLogo from './cynergylogo1.png';
import { ImageGrade } from './components/ImageGrade';
import sampleData from './config/sampleData';
import services from './services';
import XTRLogo from './XTR-logo.png';

import { TelusCompare } from './components/TelusCompare';
import { XTRCompare } from './components/XTRCompare';

const { Header, Content } = Layout;
const { Panel } = AntCollapse;
const { Search } = Input;
const { TabPane } = Tabs;
const { Title, Text } = Typography;

const provider = services[process.env.REACT_APP_PROVIDER] || services.default;

const LoadingSkeleton = (params) => {
  return (
    <>
      <Skeleton active />
      <Divider />
      <Skeleton active />
      <Divider />
      <Skeleton active />
    </>
  );
};

const DeviceDetail = (props) => {
  const { searchResults } = props;

  const [deviceData] = searchResults;

  return (
    <Collapse ghost expandIconPosition='right' defaultActiveKey={['details', 'cosmetic']}>
      <Panel header={<CollapsibleHeader level={3}>Device Info</CollapsibleHeader>} key='details'>
        <Row>
          <Col span={6}>
            <b>IMEI: </b> <span>{deviceData.imei_sn}</span>
          </Col>
          <Col span={6}>
            <b>Product: </b> <span>{deviceData.product_name}</span>
          </Col>
          <Col span={12}>
            <b>Model Name: </b> <span>{deviceData.model}</span>
          </Col>
        </Row>
      </Panel>
      <Panel header={<CollapsibleHeader level={3}>Functional</CollapsibleHeader>} key='functional'>
        <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
      </Panel>
      <Panel header={<CollapsibleHeader level={3}>Cosmetic</CollapsibleHeader>} key='cosmetic'>
        <CosmeticPanel searchResults={searchResults} />
      </Panel>
    </Collapse>
  );
};

const Initial = () => (
  <div style={{ textAlign: 'center', marginTop: '4rem' }}>
    <Text style={{ fontSize: '2rem' }}>Check Your Device Condition</Text>

    <Empty
      description={<Text style={{ fontSize: '1rem' }}>Please enter your IMEI</Text>}
      image={Empty.PRESENTED_IMAGE_SIMPLE}
    />
  </div>
);

const NoResults = () => (
  <Empty
    style={{ marginTop: '4rem' }}
    description={
      <Text style={{ fontSize: '1rem' }}>
        We're sorry, but this IMEI could not be found. Please check your information and try again.
      </Text>
    }
  />
);

const ErrorOnSearch = () => (
  <Result status='warning' title={`We're sorry, there was a problem. Please try again.`} />
);

const App = () => {
  const [initial, setInitial] = useState(true);
  const [loading, setLoading] = useState(false);
  const [searchResults, setSearchResults] = useState([]);
  const [error, setError] = useState(false);

  const searchIMEI = async (query) => {
    setLoading(true);

    try {
      const items = await provider.findByImei(query);

      setSearchResults(items);
    } catch (err) {
      console.log(err);
      setError(true);
    }

    setLoading(false);
  };

  const onSearch = (query) => {
    if (query === '') return;

    setInitial(false);
    setLoading(true);
    setError(false);

    if (query === 'test') {
      setLoading(false);
      setSearchResults(sampleData);
    } else if (query === 'e') {
      setLoading(false);
      setSearchResults([]);
    } else {
      searchIMEI(query);
    }
  };

  const logo = process.env.REACT_APP_PROVIDER === 'XTR' ? XTRLogo : TelusLogo;

  return (
    <Layout
      style={{
        minHeight: '100vh',
        backgroundColor: 'white',
        flexDirection: 'column',
        alignItems: 'center',
      }}
    >
      <Header
        style={{
          width: '100%',
          backgroundColor: 'white',
          borderBottom: '1px solid rgb(216, 216, 216)',
          display: 'flex',
          justifyContent: 'center',
          padding: 0,
        }}
      >
        <div
          style={{
            width: '100%',
            maxWidth: '980px',
            padding: '0 1rem',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
          }}
        >
          <img
            style={{ maxHeight: '30px' }}
            src={logo}
            alt={`${process.env.REACT_APP_PROVIDER} Logo`}
          />
        </div>
      </Header>

      <Content
        style={{
          display: 'flex',
          flexDirection: 'column',
          width: '100%',
          maxWidth: '980px',
          padding: '1rem',
        }}
      >
        <div style={{ width: '15rem' }}>
          <Search
            placeholder='IMEI'
            allowClear
            enterButton
            loading={loading}
            // onPressEnter={}
            onSearch={onSearch}
          />
        </div>
        <Divider />
        {(() => {
          if (loading) {
            return <LoadingSkeleton />;
          } else if (error) {
            return <ErrorOnSearch />;
          } else if (initial) {
            return <Initial />;
          } else {
            if (searchResults?.length > 0) {
              return <DeviceDetail searchResults={searchResults} />;
            } else {
              return <NoResults />;
            }
          }
        })()}
      </Content>

      <Footer>&copy; Copyright {new Date().getFullYear()}, Cynergy. All Rights Reserved</Footer>
    </Layout>
  );
};

const CosmeticPanel = ({ searchResults }) => {
  const compareNeeded = searchResults.length > 1;

  const [latest] = searchResults;
  const Compare = process.env.REACT_APP_PROVIDER === 'XTR' ? XTRCompare : TelusCompare;

  return (
    <Tabs>
      <TabPane tab='Latest' key='latest' forceRender>
        <div style={{ padding: '1rem 0' }}>
          <CosmeticDetail deviceData={latest} />
        </div>
      </TabPane>

      {compareNeeded && (
        <TabPane tab='Compare' key='compare' forceRender>
          <div style={{ padding: '1rem 0' }}>
            <Compare searchResults={searchResults} />
          </div>
        </TabPane>
      )}
    </Tabs>
  );
};

const formatImageGradeData = (data, { label, image, grade }) => ({
  label,
  image: data[image],
  grade: data[grade],
});

const CosmeticDetail = (props) => {
  const { deviceData } = props;
  const { grade_overall, grade_sides } = deviceData;

  /* Front, Back, Top, Bottom, Left, Right */
  /* Overall Grade, Front Grade, Back Grade, Sides Grade*/

  // TODO: Refactor Keys to one
  const frontBackKeys = [
    { label: 'Front', image: 'image_front', grade: 'grade_front' }, // Front
    { label: 'Back', image: 'image_back', grade: 'grade_back' }, // Back
  ];

  const sideKeys = [
    { label: 'Top', image: 'image_top', grade: 'grade_top' },
    { label: 'Bottom', image: 'image_bottom', grade: 'grade_bottom' },
    { label: 'Left', image: 'image_left', grade: 'grade_left' },
    { label: 'Right', image: 'image_right', grade: 'grade_right' },
  ];

  const testDetails = [
    { key: 'test_id', label: 'Test ID' },
    { key: 'start_time', label: 'Start Time' },
    { key: 'end_time', label: 'End Time' },
    { key: 'elapsed_time', label: 'Elapsed Time' },
    { key: 'test_profile', label: 'Test Profile' },
  ];

  const frontBack = frontBackKeys.map((side) => formatImageGradeData(deviceData, side));
  const sides = sideKeys.map((side) => formatImageGradeData(deviceData, side));

  return (
    <ColumnContainer>
      <ColumnContainer>
        <Descriptions size='small' labelStyle={{ fontWeight: 'bold' }}>
          {testDetails.map(({ key, label }) => (
            <Descriptions.Item key={key} label={label}>
              {deviceData[key]}
            </Descriptions.Item>
          ))}
        </Descriptions>
        <Divider />
        <div>
          <Title level={4} style={{ marginBottom: 0 }}>
            Overall Grade
          </Title>
          <Grade>{grade_overall || '-'}</Grade>
        </div>
        <FrontBackGallery>
          {frontBack.map((props) => (
            <ImageGrade key={props.label} {...props} />
          ))}
        </FrontBackGallery>
      </ColumnContainer>
      <Divider />
      <ColumnContainer>
        <div>
          <Title level={4} style={{ marginBottom: 0 }}>
            Sides Grade
          </Title>
          <Grade>{grade_sides || '-'}</Grade>
        </div>
        <SidesGallery>
          {sides.map((props) => (
            <ImageGrade key={props.label} {...props} />
          ))}
        </SidesGallery>
      </ColumnContainer>
    </ColumnContainer>
  );
};

export default App;
